@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&display=swap";
@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,300;1,400;1,500;1,600&display=swap";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

h1 {
  font-size: 34pt;
  font-weight: 800;
}

h2 {
  font-size: 17pt;
  font-weight: 600;
}

h3 {
  font-size: 15pt;
  font-weight: 400;
}

h4 {
  font-size: 13pt;
  font-weight: 300;
}

h5 {
  font-size: 10pt;
}

a, a:hover, a:visited {
  color: #f5f5f5;
  text-decoration: none;
}

body {
  color: #f5f5f5;
  background: #121212;
  padding: 0;
  font-family: Inter, Roboto, sans-serif;
  font-weight: 400;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

input, label, select, button, textarea {
  vertical-align: middle;
  white-space: normal;
  font-display: swap;
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  font-weight: 400;
  line-height: 1;
  display: inline-block;
}

body {
  overflow-x: hidden;
}

::-webkit-scrollbar {
  height: 7px;
  width: 7px;
  background-color: #0000;
  scroll-padding: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #2e2e2e;
  border-radius: 5px;
  transition: all 2s linear;
}

.hide {
  opacity: 0 !important;
  display: none !important;
}

* {
  transition: all 10ms;
}

hr {
  border-color: #87878796;
}

.fb {
  display: flex;
}

.fb-v {
  flex-direction: column;
  display: flex;
}

.center {
  align-items: center;
}

body {
  height: 100vh;
  overflow-y: auto;
}

.container {
  max-width: 320px;
  min-height: 78vh;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  margin: 20px auto;
  padding: 10px 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    min-width: 1280px;
    padding: 0 32px;
  }
}

:root {
  --primary-light: #8abdff;
  --primary: #6d5dfc;
  --primary-dark: #5b0eeb;
  --white: #fff;
  --greyLight-1: #e4ebf5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;
}

.components {
  width: 75rem;
  height: 40rem;
  box-shadow: .8rem .8rem 1.4rem var(--greyLight-2), -.2rem -.2rem 1.8rem var(--white);
  grid-template-columns: 17.6rem 19rem 20.4rem;
  grid-template-rows: repeat(autofit, min-content);
  grid-column-gap: 5rem;
  grid-row-gap: 2.5rem;
  border-radius: 3rem;
  align-items: center;
  padding: 4rem;
  display: grid;
}

.switch {
  grid-gap: 3rem;
  grid-column: 1 / 2;
  grid-template-columns: repeat(2, min-content);
  justify-self: center;
  display: grid;
}

.switch input {
  display: none;
}

.switch__1, .switch__2 {
  width: 6rem;
}

.switch__1 label, .switch__2 label {
  width: 100%;
  height: 3rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  background: #fff0;
  border-radius: 1.6rem;
  align-items: center;
  display: flex;
  position: relative;
}

.switch__1 label:after, .switch__2 label:after {
  content: "";
  width: 2.1rem;
  height: 2.1rem;
  background: var(--greyDark);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  left: .4rem;
}

.switch__1 label:before, .switch__2 label:before {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background: linear-gradient(330deg, var(--primary-dark) 0%, var(--primary) 50%, var(--primary-light) 100%);
  opacity: 0;
  transition: all .4s;
}

.switch input:checked ~ label:before {
  opacity: 1;
}

.switch input:checked ~ label:after {
  background: var(--greyLight-1);
  left: 57%;
}

.checkbox {
  grid-gap: 3rem;
  grid-column: 1 / 2;
  grid-template-columns: repeat(2, 6rem);
  justify-content: center;
  display: grid;
}

.checkbox input {
  display: none;
}

.checkbox__1, .checkbox__2 {
  width: 6rem;
  justify-content: center;
  display: flex;
}

.checkbox__1 label, .checkbox__2 label {
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  width: 2.8rem;
  height: 2.8rem;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.checkbox__1 label:hover i, .checkbox__2 label:hover i {
  color: var(--primary);
}

.checkbox__1 label i, .checkbox__2 label i {
  color: var(--greyDark);
  font-size: 1.8rem;
  font-weight: 700;
  transition: all .3s;
}

.checkbox__1 input:checked ~ label, .checkbox__2 input:checked ~ label {
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
}

.checkbox__1 input:checked ~ label i, .checkbox__2 input:checked ~ label i {
  color: var(--primary);
}

.radio {
  grid-column: 1 / 2;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  display: grid;
}

.radio input {
  display: none;
}

.radio__1 input:checked ~ label, .radio__2 input:checked ~ label {
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
}

.radio__1 input:checked ~ label:after, .radio__2 input:checked ~ label:after {
  background: var(--primary);
}

.radio__1 label, .radio__2 label {
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  width: 2.8rem;
  height: 2.8rem;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.radio__1 label:hover:after, .radio__2 label:hover:after {
  background: var(--primary);
}

.radio__1 label:after, .radio__2 label:after {
  content: "";
  width: 1.4rem;
  height: 1.4rem;
  background: var(--greyDark);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
}

.btns {
  width: 15rem;
  height: 4rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  border-radius: 1rem;
  justify-content: center;
  justify-self: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.btns__primary {
  background: var(--primary);
  box-shadow: inset .2rem .2rem 1rem var(--primary-light), inset -.2rem -.2rem 1rem var(--primary-dark), .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  color: var(--greyLight-1);
  grid-area: 4 / 1 / 5 / 2;
}

.btns__primary:hover {
  color: var(--white);
}

.btns__primary:active {
  box-shadow: inset .2rem .2rem 1rem var(--primary-dark), inset -.2rem -.2rem 1rem var(--primary-light);
}

.btns__secondary {
  color: var(--greyDark);
  grid-area: 5 / 1 / 6 / 2;
}

.btns__secondary:hover {
  color: var(--primary);
}

.btns__secondary:active {
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
}

.btns p {
  font-size: 1.6rem;
}

.clock {
  width: 12rem;
  height: 12rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  border-radius: 50%;
  grid-area: 1 / 2 / 3 / 3;
  justify-content: center;
  justify-self: center;
  align-items: center;
  display: flex;
  position: relative;
}

.clock .hand {
  transform-origin: bottom;
  z-index: 200;
  border-radius: .2rem;
  position: absolute;
  bottom: 6rem;
}

.clock .hours {
  width: .4rem;
  height: 3.2rem;
  background: var(--greyLight-3);
}

.clock .minutes {
  width: .4rem;
  height: 4.6rem;
  background: var(--greyDark);
}

.clock .seconds {
  width: .2rem;
  height: 5.2rem;
  background: var(--primary);
}

.clock .point {
  width: .8rem;
  height: .8rem;
  background: var(--primary);
  z-index: 300;
  border-radius: 50%;
  position: absolute;
}

.clock .marker {
  width: 95%;
  height: 95%;
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  border-radius: 50%;
  position: relative;
}

.clock .marker:after {
  content: "";
  width: 60%;
  height: 60%;
  box-shadow: inset 1px 1px 1px var(--greyLight-2), inset -1px -1px 1px var(--white);
  filter: blur(1px);
  border-radius: 50%;
  position: absolute;
  top: 20%;
  left: 20%;
}

.clock .marker__1, .clock .marker__2, .clock .marker__3, .clock .marker__4 {
  box-shadow: inset 1px 1px 1px var(--greyLight-2), inset -1px -1px 1px var(--white);
  border-radius: .1rem;
  position: absolute;
}

.clock .marker__1, .clock .marker__2 {
  width: .2rem;
  height: .6rem;
  left: 5.6rem;
}

.clock .marker__3, .clock .marker__4 {
  width: .6rem;
  height: .2rem;
  top: 5.6rem;
}

.clock .marker__1 {
  top: 2%;
}

.clock .marker__2 {
  top: 98%;
  transform: translateY(-.6rem);
}

.clock .marker__3 {
  left: 2%;
}

.clock .marker__4 {
  left: 98%;
  transform: translateX(-.6rem);
}

.chip {
  width: 17rem;
  height: 4rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  border-radius: 1rem;
  grid-area: 3 / 2 / 4 / 3;
  justify-content: space-between;
  justify-self: center;
  align-items: center;
  display: flex;
}

.chip__icon {
  width: 3rem;
  height: 3rem;
  color: var(--primary);
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 .2rem;
  font-size: 1.8rem;
  display: flex;
}

.chip p {
  color: var(--greyDark);
  margin-left: -1.8rem;
  font-size: .9rem;
}

.chip__close {
  width: 1.6rem;
  height: 1.6rem;
  color: var(--greyLight-3);
  cursor: pointer;
  margin: 0 .5rem;
  font-size: 1.6rem;
  display: flex;
}

.circle {
  width: 9rem;
  height: 100%;
  border-radius: 1rem;
  grid-area: 4 / 2 / 6 / 3;
  grid-template-rows: 1fr;
  justify-self: center;
  place-items: center;
  display: grid;
}

.circle__btn {
  width: 6rem;
  height: 6rem;
  color: var(--primary);
  z-index: 300;
  background: var(--greyLight-1);
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  border-radius: 50%;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: center;
  align-items: center;
  margin: .6rem;
  font-size: 3.2rem;
  display: flex;
  position: relative;
}

.circle__btn.shadow {
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
}

.circle__btn .play {
  opacity: 0;
  transition: all .2s linear;
  position: absolute;
}

.circle__btn .play.visibility {
  opacity: 1;
}

.circle__btn .pause {
  transition: all .2s linear;
  position: absolute;
}

.circle__btn .pause.visibility {
  opacity: 0;
}

.circle__back-1, .circle__back-2 {
  width: 6rem;
  height: 6rem;
  filter: blur(1px);
  z-index: 100;
  border-radius: 50%;
  grid-area: 1 / 1 / 2 / 2;
}

.circle__back-1 {
  box-shadow: .4rem .4rem .8rem var(--greyLight-2), -.4rem -.4rem .8rem var(--white);
  background: linear-gradient(to bottom right, var(--greyLight-2) 0%, var(--white) 100%);
  animation: 4s linear infinite waves;
}

.circle__back-1.paused {
  animation-play-state: paused;
}

.circle__back-2 {
  box-shadow: .4rem .4rem .8rem var(--greyLight-2), -.4rem -.4rem .8rem var(--white);
  animation: 4s linear 2s infinite waves;
}

.circle__back-2.paused {
  animation-play-state: paused;
}

.form {
  grid-area: 3 / 3 / 4 / 4;
}

.form__input {
  width: 20.4rem;
  height: 4rem;
  color: #f5f5f5;
  background: none;
  border: none;
  border-radius: 1rem;
  padding-left: 1.4rem;
  font-family: inherit;
  font-size: 1.4rem;
}

.form__input::placeholder {
  color: #888;
}

.form__input:focus {
  outline: none;
}

.search {
  grid-area: 2 / 3 / 3 / 4;
  align-items: center;
  display: flex;
  position: relative;
}

.search__input {
  width: 20.4rem;
  height: 4rem;
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  color: var(--greyDark);
  background: none;
  border: none;
  border-radius: 1rem;
  padding-left: 3.8rem;
  font-family: inherit;
  font-size: 1.4rem;
}

.search__input::placeholder {
  color: var(--greyLight-3);
}

.search__input:focus {
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  outline: none;
}

.search__input:focus + .search__icon {
  color: var(--primary);
}

.search__icon {
  height: 2rem;
  color: var(--greyDark);
  padding: 0 1rem;
  font-size: 2rem;
  transition: all .3s;
  display: flex;
  position: absolute;
}

.segmented-control {
  width: 20.4rem;
  height: 4rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  border-radius: 1rem;
  grid-area: 1 / 3 / 2 / 4;
  align-items: center;
  display: flex;
  position: relative;
}

.segmented-control input {
  display: none;
}

.segmented-control > input:checked + label {
  color: var(--primary);
  transition: all .5s;
}

.segmented-control__1, .segmented-control__2, .segmented-control__3 {
  width: 6.8rem;
  height: 3.6rem;
  cursor: pointer;
  color: var(--greyDark);
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  transition: all .5s;
  display: flex;
}

.segmented-control__1:hover, .segmented-control__2:hover, .segmented-control__3:hover {
  color: var(--primary);
}

.segmented-control__color {
  height: 3.4rem;
  width: 6.2rem;
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  pointer-events: none;
  border-radius: .8rem;
  margin-left: .3rem;
  position: absolute;
}

#tab-1:checked ~ .segmented-control__color {
  transition: transform .3s cubic-bezier(.645, .045, .355, 1);
  transform: translateX(0);
}

#tab-2:checked ~ .segmented-control__color {
  transition: transform .3s cubic-bezier(.645, .045, .355, 1);
  transform: translateX(6.8rem);
}

#tab-3:checked ~ .segmented-control__color {
  transition: transform .3s cubic-bezier(.645, .045, .355, 1);
  transform: translateX(13.6rem);
}

.icon {
  grid-area: 4 / 3 / 5 / 4;
  justify-content: space-between;
  display: flex;
}

.icon__account, .icon__home, .icon__settings {
  width: 4rem;
  height: 4rem;
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  cursor: pointer;
  color: var(--greyDark);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  transition: all .5s;
  display: flex;
}

.icon__account:active, .icon__home:active, .icon__settings:active {
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  color: var(--primary);
}

.icon__account:hover, .icon__home:hover, .icon__settings:hover {
  color: var(--primary);
}

.slider {
  flex-direction: column;
  grid-area: 5 / 3 / 6 / 4;
  align-self: center;
  display: flex;
}

.slider__box {
  width: 100%;
  height: 1rem;
  cursor: pointer;
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.slider__btn {
  width: 2rem;
  height: 2rem;
  background: var(--white);
  box-shadow: 0px .1rem .3rem 0px var(--greyLight-3);
  z-index: 200;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.slider__btn:hover ~ .slider__tooltip {
  opacity: 1;
}

.slider__btn:after {
  content: "";
  width: .8rem;
  height: .8rem;
  box-shadow: inset .2rem .2rem .5rem var(--greyLight-2), inset -.2rem -.2rem .5rem var(--white);
  border-radius: 50%;
  position: absolute;
}

.slider__color {
  height: 100%;
  width: 50%;
  z-index: 100;
  border-radius: inherit;
  background: var(--primary);
  background: linear-gradient(-1deg, var(--primary-dark) 0%, var(--primary) 50%, var(--primary-light) 100%);
  position: absolute;
  left: 0;
}

.slider__tooltip {
  height: 2.5rem;
  width: 3rem;
  color: var(--primary);
  box-shadow: .3rem .3rem .6rem var(--greyLight-2), -.2rem -.2rem .5rem var(--white);
  opacity: 0;
  border-radius: .6rem;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  top: 2.6rem;
}

@keyframes waves {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.dribbble {
  color: #ea4c89;
  font-size: 2.6rem;
  position: fixed;
  bottom: 1rem;
  right: 2rem;
}

input {
  height: 40px;
  width: 230px;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #f5f5f5;
  background: #2a2a2a;
  border: none;
  border-radius: 50px;
  outline: none;
  padding: 0 20px;
  font-size: medium;
  overflow: hidden;
}

input:valid, textarea:valid {
  box-shadow: 0 2px 5px -1px #3b9e4526, 0 1px 3px -1px #3b9e4540 !important;
}

input:invalid, textarea:invalid {
  box-shadow: 0 2px 5px -1px #9e3b3b26, 0 1px 3px -1px #ff000040 !important;
}

.btn {
  letter-spacing: .5px;
  text-transform: uppercase;
  text-align: center;
  height: 50px;
  width: 100%;
  min-width: auto;
  color: #fefefe;
  cursor: pointer;
  border: 1px solid #a2a2a280;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 5px 0;
  padding: 0 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  display: flex;
  position: relative;
}

.btn-primary {
  background-color: #c31011;
  border: none;
}

.btn:hover {
  box-shadow: 0 3px 10px #d8d8d81a;
}

select.custom-select {
  color: #f5f5f5;
  min-width: 150px;
  appearance: none;
  text-align: center;
  background-color: #121212;
  border-bottom: 1px solid #80808074;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  font-size: medium;
}

select::-ms-expand {
  display: none;
}

.custom-select {
  height: 70px;
  cursor: pointer;
  flex-direction: column;
